import { Box, Flex, Img, Text } from '@chakra-ui/react'
import React from 'react'
import Rishav from "../Components/Resources/Rishav.jpg"
const Home = () => {
  return (
    <Box>
    <Flex textColor={"white"} h={"full"} backgroundColor={"blue.700"} fontSize={"19px"} textAlign="left">
      <Box paddingLeft="200px">
      <Text marginTop="5px" fontSize={"46px"}>Hello .</Text>
      <Text>I am Rishav Neogi</Text>
      </Box>
      <Img marginTop={"25px"} w={"25%"} h={"330px"} borderRadius={"50%"} src={Rishav} alt='port' />
    </Flex>
    </Box>
  )
}

export {Home}